@ccclass('HandleGraphics')
export class HandleGraphics extends Component {


    graphics: Graphics = null;


    protected start(): void {
        this.graphics = this.getComponent(Graphics);
    }


    public drawGraphics(moveTo: Vec3, LineTo: Vec3): void {
        this.graphics.moveTo(moveTo.x, moveTo.y);
        this.graphics.lineTo(LineTo.x, LineTo.y);
        this.graphics.stroke();
        log("Drawing");
    }


    public clearGraphics(): void {
        this.graphics.clear();
        log("Clear Graphics");
    }
}

